import React, { Component } from 'react';
import PropTypes from 'prop-types';

var scriptURL = 'https://weatherwidget.io/js/widget.min.js';

/**
* Component loading the weather forecast from 'WeatherWidget.io' for
* a specified location (defaults to 'Schwaebisch-Gmuend')
*/
class WeatherWidget extends Component {

	constructor(props) {
		super(props);
		if(document.querySelector('script[src="' + scriptURL + '"]') === null) {
			this.loadScript(scriptURL);
		}
	}

  componentDidMount() {
		this.loadScript(scriptURL)
	}

	loadScript(url) {
	    var script = document.createElement("script")
	    script.type = "text/javascript";
	    // script.async = true;
	    // script.defer = true;
	    script.src = url;
	    document.getElementsByTagName("body")[0].appendChild(script);
	}

	render() {
		return(
			<a
        class = "weatherwidget-io"
				id = {this.props.id}
        href = {`https://forecast7.com/en/48d809d80/${this.props.location}/`}
      >
			</a>
			);
	}
};

export default WeatherWidget;

WeatherWidget.defaultProps = {
	id: 'weatherwidget-io',
  location: 'schwabisch-gmund'
};

WeatherWidget.propTypes = {
	/**
	* Id to which map DOm elements will be bound to as children
	*/
  id: PropTypes.string,
	/**
	* Location
	*/
	location: PropTypes.string
};
